.popup__title{
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
    box-sizing: border-box;
    padding: 34px 0 0 36px;
    margin: 0;
}

@media screen and (max-width:550px) {
    .popup__title{
        font-size: 18px;
        line-height: 22px;
        padding: 25px 0 0 22px;
    }
}