.popup__input{
    width: 100%;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    padding-bottom: 8px;
    box-sizing: border-box;
}


.popup__input::placeholder {
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #C4C4C4;
}